/* 
  #Self-Intro
    .inner-wrapper
      #signature
      #text
        .email-link
*/
#Self-Intro {
  position: relative;
  font-family: 'Cutive Mono';
  color: #fff;
  -webkit-transition: height 0.7s;
  -o-transition: height 0.7s;
  transition: height 0.7s; }
  @media screen and (max-width: 480px) {
    #Self-Intro {
      width: 100%;
      display: block;
      margin: 0 0 100px;
      padding: 75px 0 40px; } }
  @media screen and (min-width: 481px) and (max-width: 1279px) {
    #Self-Intro {
      width: 420px;
      margin: 0 auto 100px;
      padding: 50px 0 40px; } }
  @media screen and (min-width: 1280px) {
    #Self-Intro {
      width: 300px;
      display: inline-block;
      position: fixed;
      bottom: 75px; } }
  #Self-Intro:after {
    content: '';
    position: absolute;
    background-color: rgba(255, 255, 255, 0.45); }
    @media screen and (max-width: 1279px) {
      #Self-Intro:after {
        width: 100%;
        height: 3px;
        bottom: -20px;
        -webkit-box-shadow: 0 5px 1px rgba(255, 255, 255, 0.35), 0 10px 2px rgba(255, 255, 255, 0.25);
                box-shadow: 0 5px 1px rgba(255, 255, 255, 0.35), 0 10px 2px rgba(255, 255, 255, 0.25);
        -webkit-transform: skewY(-8deg);
            -ms-transform: skewY(-8deg);
                transform: skewY(-8deg); } }
  #Self-Intro .inner-wrapper {
    width: 100%;
    margin: 0 auto; }
  #Self-Intro #signature {
    opacity: 0.85; }
    @media screen and (max-width: 1279px) {
      #Self-Intro #signature {
        width: 80%;
        height: auto;
        display: block;
        margin: 0 auto; } }
    @media screen and (min-width: 1280px) {
      #Self-Intro #signature {
        width: 90%;
        height: auto; } }
  #Self-Intro #text {
    text-align: center;
    line-height: 1.2; }
    @media screen and (max-width: 480px) {
      #Self-Intro #text {
        width: 80%;
        margin: 0 10%; } }
    @media screen and (min-width: 1280px) {
      #Self-Intro #text {
        font-size: 18px; } }
    #Self-Intro #text .email-link {
      color: white; }



